//NEW IN   -----------------------------------------------------------
function NewIn() {
  let newinData = newin;

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    autoplay: true,
    autoplaySpeed:4000
  };
  return (
    <>
      <div
        className="container"
        style={{  marginTop: "60px" }}
      >
        <h1 className="title">NEW IN</h1>
        <p
          style={{ fontSize: "16px", color: "#1A1A1A", letterSpacing: "-1px" }}
        >
          ,       .
        </p>
      </div>

      <div className="container">
        <Slider {...settings}>
          {newinData.map((ele, i) => (
            <div key={i} className="col-md-3">
              <div className="imgBox" style={{ width: "95%" }}>
                <img
                  src={"/types2/img/new_in/" + newinData[i].imgUrl}
                  style={{ width: "100%", height: "100%", cursor: "pointer" }}
                />
              </div>
              <div className="textBox" style={{ marginTop: "15px" }}>
                <h3 className="h3Style">{newinData[i].title}</h3>

                <h4 className="h4Style">{newinData[i].code}</h4>
                <p className="Pstyle">{newinData[i].price}</p>
              </div>
            </div>
          ))}
        </Slider>
      </div>
    </>
  );
}




