// BEST ITEM -------------------------------------------------
function Best(): JSX.Element {
  
  let [bestItem, setBestItem] = useState(best_items);

  let [count , setCount]= useState<number>(1);

  

  return (
    <>
      <div
        className="container"
        style={{marginTop: "80px" }}
      >
        <div style={{ display: "flex", alignItems: "center" }}>
          <h1 className="title">BEST ITEM</h1>
          <span className="small_t">
                .{" "}
          </span>
        </div>
      </div>


      <div className="container" style={{textAlign:"center", marginTop:"30px"}}>
        <button type="button" className="btn btn-outline-primary" onClick={()=>{
          if(count==1){
            axios
              .get(
                "https://rlaquf1004.github.io/reacte_MB_data/bestData.json"
              )
              .then ((plus)=>{
                let copy = [...bestItem, ...plus.data];
                setBestItem(copy);
                setCount(2);
              })
          }else {
            alert("   ")
          }
        }}> </button>
      </div>


      <div className="container" style={{marginTop:"30px"}}>
        
        <div className="row">
          {bestItem.map((ele, i) => {
            return (
              <>
                <div className="col-md-3">
                  <div className="Bst_imgBox">
                    <img src={ bestItem[i].imgUrl} />
                  </div>
                  <div className="Bst_textBox">
                    <h3 className="h3Style">{bestItem[i].title}</h3>
                    <h4 className="h4Style">{bestItem[i].code}</h4>
                    <p className="Pstyle">{bestItem[i].price}</p>
                  </div>
                </div>
              </>
            );
          })}
        </div>
      </div>
    </>
  );
}



