import React from "react";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addItem } from "./store";
import { Link } from "react-router-dom";
// 

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css/pagination";
import { Pagination } from "swiper";
import "swiper/css";
// 

import Form from "react-bootstrap/Form";
import { Nav } from "react-bootstrap";
// 

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
// 


//  
import {TopSellerType} from "./App"


//     .
interface ProductProps {
  seller: TopSellerType[]; //  
}



//  
interface CustomerListType {
  name: string;
  message: string;
  size: string;
}



//  ,   
interface PropsType {
  tap: number;
  product: {
    subImgProd1: string;
    subImgProd2: string;
    subImgProd3: string;
    subImgProd4: string;
    subImgProd5: string;
    
  };
}


// 
function Product(props:ProductProps): JSX.Element {
  let [fade2, setFade2] = useState('')


  useEffect(()=>{

    window.scrollTo(0, 0);

         setFade2('end')

         return ()=>{

              setFade2('')

         }

},[])


  // window.scrollTo(0, 0);


  let { id } = useParams(); //    &   

  const [product, setProduct] = useState<TopSellerType | null>({
  id: 0,
  imgUrl: "",
  title: "",
  code: "",
  price: "",
  subImgProd1: "",
  subImgProd2: "",
  subImgProd3: "",
  subImgProd4: "",
  subImgProd5: "",}); //   

  let [tap, setTap] = useState<number>(0);

  let dispatch = useDispatch();
  //   .

  useEffect(() => {
    window.scrollTo(0, 0);

    // id    
    const GetProduct = props.seller.find((item) => item.id === parseInt(id|| "")); //seller id    id .   id    GetProduct . =    .
    setProduct(GetProduct || null); //     .
  }, [id, setProduct]); //id      useEffect .

  if (product === null) {
    /*  null  if     .*/
    return <div>Loading...</div>;
  }

  return (
    <div className="start ex">
      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <Swiper
              spaceBetween={30}
              pagination={{
                clickable: true,
              }}
              modules={[Pagination]}
              className="mySwiper"
            >
              <SwiperSlide>
                <img
                  src={"/types2/img/top_seller/" + product.imgUrl} //   seller 
                  width="100%"
                />
              </SwiperSlide>
              <SwiperSlide>
                <img
                  src={"/types2/img/top_seller/" + product.imgUrl2} //   seller 
                  width="100%"
                />
              </SwiperSlide>
            </Swiper>
          </div>

          <div className="col-md-6">
            <div className="productBox">
              <div className="pro_info">
                <div className="Title_box">
                  <h2>{product.title}</h2>
                </div>
                {/*  */}

                <div className="Price_box">
                  <h1>{product.price}</h1>
                </div>
                {/*  */}

                <div className="Version_box">
                  <h3>Color</h3>
                  <ul>
                    <li className="thumb">
                      <img src="/types2/img/pro_mini_clothes1.jpg" />
                    </li>
                    <li className="thumb">
                      <img src="/types2/img/pro_mini_clothes2.jpg" />
                    </li>
                    <li className="thumb">
                      <img src="/types2/img/pro_mini_clothes3.jpg" />
                    </li>
                  </ul>
                </div>
              </div>

              <div className="pro_option">
                <table>
                  <tr>
                    <th></th>
                    <td>
                      <Form.Select
                        aria-label="Default select example"
                        style={{ width: "100%" }}
                      >
                        <option>- [] SELECT OPTION -</option>
                        <option>--------------------------</option>
                        <optgroup label="">
                          <option value="1">S</option>
                          <option value="2">M</option>
                          <option value="3">L</option>
                        </optgroup>
                      </Form.Select>
                    </td>
                  </tr>
                </table>
              </div>

              <h5>( 1)</h5>

              <div className="Btn_Box">
                <button
                  type="button"
                  className="btn btn-success"
                  onClick={() => {
                    dispatch(
                      addItem({
                        id: product.id,
                        code: product.code,
                        imgUrl: product.imgUrl,
                        name: product.title,
                        count: 1,
                      })
                    );
                  }}
                >
                  BUY IT NOW
                </button>

                <Link to="/cart">
                  <button type="button" className="btn btn-outline-success">
                    CART
                  </button>
                </Link>

                <Link to="/cart">
                  <button
                    type="button"
                    className="btn btn-outline-light"
                    style={{ border: "1px solid #ddd" }}
                  >
                    WISH LIST
                  </button>
                </Link>
              </div>
            </div>
          </div>

          {/* row */}
        </div>

        {/* <Nav fill variant="tabs" className="NavTabs">
          <Nav.Item>
            <Nav.Link  eventKey="link-1"></Nav.Link>
          </Nav.Item>

          <Nav.Item>
            <Nav.Link eventKey="link-2">
              <span className="reviewNum">26</span>
            </Nav.Link>
          </Nav.Item>

          <Nav.Item>
            <Nav.Link eventKey="link-3">//</Nav.Link>
          </Nav.Item>
        </Nav> */}

        <Nav
          variant="tabs"
          defaultActiveKey="link0"
          style={{ marginTop: "100px" }}
        >
          <Nav.Item style={{ width: "33%", textAlign: "center" }}>
            <Nav.Link
              onClick={() => {
                setTap(0);
              }}
              eventKey="link0"
            >
              
            </Nav.Link>
          </Nav.Item>

          <Nav.Item style={{ width: "33%", textAlign: "center" }}>
            <Nav.Link
              onClick={() => {
                setTap(1);
              }}
              eventKey="link1"
            >
              
            </Nav.Link>
          </Nav.Item>

          <Nav.Item style={{ width: "33%", textAlign: "center" }}>
            <Nav.Link
              onClick={() => {
                setTap(2);
              }}
              eventKey="link2"
            >
              //
            </Nav.Link>
          </Nav.Item>
        </Nav>


        {/*      */}
        <TabContent tap={tap} product={product} ></TabContent>

        {/* container */}
      </div>
    </div>
  );
}




//     
function TabContent(props:PropsType): JSX.Element {
  return [
    //  
    <div className="container subImgProd">
      <div className="col-md-12">
        <div style={{ width: "900px", margin: "80px auto" }}>
          <img
            src="/types2/img/sub_product/sub_img_01.jpg"
            style={{ cursor: "pointer" }}
          />

          <img src="/types2/img/sub_product/sub_img_02_MB_banner.jpg" />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd1} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd2} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd3} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd4} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd5} />

          <img src="/types2/img/sub_product/sub_img_03_clean_tip.jpg" />

          <img src="/types2/img/sub_product/sub_img_04_malbon.jpg" />

          <img src="/types2/img/sub_product/sub_img_05_malbon_delivery.jpg" />
        </div>
      </div>
    </div>,

    <ProReview />,

    <Delivery />,
  ][props.tap];
}







