//   
function ReivewComment(){


  //  
  const [customerList, setCustomerList]= useState<CustomerListType[]>([
    {
        name: "gilldong_Hong",
        message:".     ",
        size: "S"
    },
    {
      name: "",
      message:" .",
      size: "L"
  },
    {
      name: "",
      message:"       ",
      size: "M"
    }
 ]);


//  .
const [customer, setCustomer]=useState<CustomerListType>({
  name:'', 
  message:'',
  size: ""
});


//     .
const handleName = (e: React.ChangeEvent<HTMLInputElement>)=>{
  // console.log(e.target.value);
  setCustomer((prevState)=>{
    return({...prevState, name:e.target.value});
  });
};


//          .
const handlemessage = (e: React.ChangeEvent<HTMLInputElement>)=>{
  //    console.log(e.target.value);
    setCustomer((prevState)=>{
      return({...prevState, message:e.target.value});
    });
   };


// 
const selectList = [" ","S", "M", "L"];



//    
const handleSelect = (e: React.ChangeEvent<HTMLSelectElement>) => { 
  
  setCustomer((prevState)=>{
    return({...prevState, size: e.target.value})});
                    
};


  
  //   & 
  const handleCommit = (e: React.MouseEvent<HTMLButtonElement>)=>{

    if(customer.name == ""){
      alert(" ")
    } else if(customer.message == ""){
      alert(" ")
    }else if(customer.size == "" || customer.size == " "){
      alert(" ")
    }else{

      console.log(customer.name);
      console.log(customer.message);
      console.log(customer.size)
  
      //
       setCustomerList((prevState)=>{
         
           return [customer,...prevState];
       });
  
       //input 
       setCustomer(()=>{
           return {name:'', message:'', size: ""};
       });
    }
    };

  

  return (
        <div className="container" style={{marginTop:"50px"}}>
        
          {/*   */}
          <div className="Comment_input_box">
            <div style={{color:"rgb(107, 107, 107)", fontWeight:"bold"}}></div>
            <input type="text"
            onChange={handleName}
            value={customer.name}
            style={{ width: "70%", height: "35px" ,borderRadius:"5px", border:"1px solid #91949c" }}
            placeholder=" ."
            ></input>

            <div style={{color:"rgb(107, 107, 107)", fontWeight:"bold"}}> </div>
            <input name="message" type="text" onChange={handlemessage}
            value={customer.message}
            style={{width:"70%", height:"100px" , borderRadius:"5px", border:"1px solid #91949c"}}
            placeholder=" ."
            ></input>

            <div style={{color:"rgb(107, 107, 107)", fontWeight:"bold"}}> </div>
            <select onChange={handleSelect} value={customer.size} style={{width:"10%", borderRadius:"5px", border:"1px solid #91949c",}}>
              {selectList.map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
              ))}
            </select>

            
            <button type="button" className="btn btn-outline-warning" style={{width:"15%", fontWeight:"bold"}}
            onClick={handleCommit}
            > </button>
          </div>


          {/*   */}
          <div className="resultBox" >

            <div className="col-md-12">
              {customerList.map((ele,i)=>{

                return (
                  <div key={i} className="commentBox">

                    <div className="comBoxInUser">
                      <div className="userImgBox"><img src="/types2/img/malbon_character.jpg" alt=" "/></div>
                      <div className="starBox2"></div>
                      <div className="userName">{ele.name}</div>
                    </div>

                    <div className="comBoxInCom">
                      <div className="sizeBox">: {ele.size}</div>
                      <div style={{display:"flex"}}>
                        <div className="messageBox">{ele.message}</div>
                        <button type="button" className="btn btn-light" onClick={()=>{
                            let copy = [...customerList];
                            copy.splice(i,1);
                            setCustomerList(copy);

                        }} style={{width:"8%", fontSize:"13px"}}></button>
                      </div>
                      
                      <div className="small_ImgBox">
                        <div className="reviewHeart">
                          <img src="/types2/img/comment.png"/>
                          <span>0</span>
                        </div>

                        <div className="reviewHeart">
                          <img src="/types2/img/heart.png"/>
                          <span>0</span>
                        </div>

                        <div className="Declaration_box">
                          <img src="/types2/img/Declaration.png"/>
                          <span></span>
                        </div>
                      </div>

                    </div>


                  </div>
                )

              })}
            </div>

          </div>



          {/*  */}
          <nav aria-label="Page navigation example" style={{marginTop:"60px"}}>
            <ul className="pagination justify-content-center">
              <li className="page-item disabled">
                <a className="page-link" href = "#"  tabIndex={-1}> &lt;</a>
              </li>
              <li className="page-item"><a className="page-link" href="#">1</a></li>
              <li className="page-item"><a className="page-link" href="#">2</a></li>
              <li className="page-item"><a className="page-link" href="#">3</a></li>
              <li className="page-item">
                <a className="page-link" href="#">&gt;</a>
              </li>
            </ul>
          </nav>

        
        
        {/*   container */}
        </div>
  )

}





