import { configureStore, createSlice } from '@reduxjs/toolkit' 
//  .   


interface UserState {
    name: string;
}

interface CartItemType {
    id: number;
    code:string;
    imgUrl: string;
    name: string;
    count: number;
}



//   
let user = createSlice({
    name : 'user',
    initialState : { name : '' } ,
    reducers: {}
})



//  state
let cart = createSlice({
    name : 'cart',
    initialState : [ 

    //    
    {id: 0 , code : "M3122PKT15OWT" , imgUrl:'top_seller1.jpg', name : '   OFF WHITE', count : 1}, 
    {id: 1 , code : "M3122PKT17CRM" , imgUrl:'top_seller2.jpg', name : '   CREAM', count : 1},
    {id: 2 , code : "M3222PTS34GRN" , imgUrl:'top_seller3.jpg', name : '    GREEN', count : 1}
    ]as CartItemType[],

    // 
    reducers : {

    //    
    addCount(state, action) {
        let num = state.findIndex((a) => {
        return a.code === action.payload;
    });
        console.log(num);
        state[num].count++;
    },

    //     - 0   
    decreaseCount(state, action) {
        let num = state.findIndex((a) => {
        return a.code === action.payload;
        });
        console.log(num);
        if (state[num].count > 0) {
        state[num].count--;
        } else if (state[num].count === 0) {
        alert("   .");
        }
    },

    //     ,    .
    addItem(state, action) {
        let num = state.findIndex((a) => a.code === action.payload.code);
        if (num !== -1) {
        state[num].count++;
        } else {
        state.push(action.payload);
        }
    },

    //   . 
    deleteItem(state, action) {
        let num = state.findIndex((a) => {
        return a.code === action.payload;
        });
        state.splice(num, 1);
        //      1 .
    },

    //  
    sortName(state) {
        state.sort((a, b) => (a.name > b.name ? 1 : -1));
    }
    }
})

// addCount(1)
//    .
export let { addCount, decreaseCount, addItem, deleteItem, sortName } = cart.actions;




//     .  
export default configureStore({
    reducer: {
    user: user.reducer,
    cart: cart.reducer,
    },
});
