import { Table } from "react-bootstrap";
import { Button } from "react-bootstrap";
// 

import { useDispatch, useSelector } from "react-redux";
//  useDispatch: store.js   .       .
//  useSelector:    .

import { addCount, decreaseCount, deleteItem, sortName } from "./store";
//    .

import { Link } from "react-router-dom";
// 

// import { RootState } from "./store";





interface RootState {
  user: {
    name: string;
  };
  cart: CartItem[];
}

interface CartItem {
  id: number;
  code: string;
  imgUrl: string;
  name: string;
  count: number;
}





//   
function Cart(): JSX.Element {

  //  .
  let state = useSelector((state: RootState) => state);

  let dispatch = useDispatch();

// 
  const smallProdcuctStyle: React.CSSProperties = {
    border: '1px solid #ddd',
    width: '60px',
    height: '60px',
    cursor:'pointer'
} ;

//    
let textverticalAlign: React.CSSProperties ={
   verticalAlign: 'middle', 
   textAlign: 'center' 
} 



  return (
    <div className="container">
      <h5 style={{ padding: "50px" , textAlign:"center"}}>
        {state.user.name} 
      </h5>
      
      <Table>
        <thead>
          <tr style={{borderBottom:"2px solid #000"}}>
            <th style={textverticalAlign}></th>
            <th style={textverticalAlign}></th>
            <th style={textverticalAlign}></th>
            <th style={textverticalAlign}></th>
            <th style={textverticalAlign}></th>
          </tr>
        </thead>

        <tbody >
          {state.cart.map((a: CartItem, i:number) => (
            <tr key={i} style={{borderBottom:"1px solid rgb(204, 204, 204)"}}>
              <td style={textverticalAlign}>{state.cart[i].code}</td>

              <td style={textverticalAlign}>
                <Link to={`/Product/${state.cart[i].id}`}>
                  <img
                    src={`img/top_seller/${state.cart[i].imgUrl}`}
                    style={smallProdcuctStyle}
                  />
                </Link>
              </td>
              <td style={textverticalAlign}>{state.cart[i].name}</td>
              <td style={textverticalAlign}>{state.cart[i].count}</td>
              <td style={textverticalAlign}>

                {/*   */}
                <button type="button"
                  onClick={() => {
                    dispatch(addCount(state.cart[i].code));
                  }}
                  className="btn btn-light btn-outline-dark" 
                  style={{ marginRight: "10px" }}
                >
                  +
                </button>


                {/*   */}
                <button type="button"
                  onClick={() => {
                    dispatch(decreaseCount(state.cart[i].code));
                  }}
                  className="btn btn-light btn-outline-dark"
                  style={{ marginRight: "10px" }}
                >
                  -
                </button>

                {/*  */}
                <button type="button"
                  onClick={() => {
                    dispatch(deleteItem(state.cart[i].code));
                  }}
                  className="btn btn-outline-success"
                >
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      {/*   */}
      <div className="container" style={{textAlign:"center", padding:"50px"}}>
        <Button
            variant="outline-primary"
            onClick={() => {
              dispatch(sortName());
            }}
        >
            
        </Button>

      </div>
      
    </div>
  );
}

export default Cart;
